let dataCopy;
const sizeFontTable = 9;
let allNoms = [];
export function generateTable(doc, data, superData) {
    allNoms = [];
    dataCopy = data;
    textTable(doc, 'DATOS GENERALES')
    contactInformation(doc, superData.client);
}

export function generateTableDocumentation(doc, superData) {
    informationText(doc);
    if (superData.documentaryInformation.length > 0) {
        doc.addPage();
        textTable(doc, 'INFORMACIN DOCUMENTAL');
        observation(doc, superData.documentaryInformation);
    }
}


function contactInformation(doc, data) {

    const tempData = [
        {
            one:
                "NOMBRE COMERCIAL",
            two: data.tradename.toUpperCase(),
        },
        {
            one: "MUNICIPIO",
            two: data.municipality.toUpperCase(),
        },
        {
            one: "FECHA DE INSPECCIN ",
            two: data.date.toUpperCase(),
        },
        {
            one: "CONTACTO",
            two: data.whoAttendedUs.toUpperCase(),
        }
    ];


    autoTable(doc, {
        theme: 'grid',
        body: tempData,
        columnStyles: {
            one: { cellWidth: 50 },

        },
        columns: [
            { dataKey: "one" },
            { dataKey: "two" },

        ],
        styles: { fontSize: sizeFontTable, textColor: "#17202A", cellPadding: 0.5, lineColor: "#17202A" },
        margin: { left: 30, right: 30, }
    })
}

///Temporal se tiene que cambiar...
function observation(doc, data, note) {
    allNoms = [...allNoms, ...data];
    const tempDataTitle = [
        {
            one:
                "OBSERVACIN ",
            two: 'SECCIN',

        },
    ];

    const tempData = [
        ...tempDataTitle,
        ...data
    ]
    /* suponiento que hay datos en data.. hay que fusionar los datos de informacin documental */

    autoTable(doc, {
        theme: 'plain',
        body: tempData,
        columnStyles: {
            one: { cellWidth: 130 },
            two: { halign: 'center' },
        },
        columns: [
            { dataKey: "one" },
            { dataKey: "two" },

        ],
        styles: { fontSize: sizeFontTable, textColor: "#17202A", cellPadding: 0.5 },
        margin: { left: 30, right: 30, },
        didParseCell: data => {

            if (data.row.index == 0) {
                data.cell.styles.textColor = `#FFFFFF`;
                data.cell.styles.fillColor = `#85C1E9`;
            }

            if (data.row.cells[1].raw == '110-14.C') {
                data.cell.styles.textColor = `#FFFFFF`;
                data.cell.styles.fillColor = `#922B21`;
            }

            if (data.row.cells[1].raw == '408-40') {
                data.cell.styles.textColor = `#FFFFFF`;
                data.cell.styles.fillColor = `#922B21`;
            }
            if(data.row.cells[1].raw == '110-12'){
                if(data.row.cells[0].raw.includes("Debe realizar cambio total del tablero")){
                    data.cell.styles.textColor = `#FFFFFF`;
                    data.cell.styles.fillColor = `#922B21`;
                }
              
            }
        }
    })


    if (note && note.value) {

        if (note.value.replace(/ /g, "") != "") {
            autoTable(doc, {
                theme: 'plain',
                body: [['Notas'], [note.value]],
                styles: { fontSize: sizeFontTable, textColor: "#17202A", cellPadding: 0.5 },
                margin: { left: 30, right: 30, },
            })
        }

    }


    doc.setDrawColor('#17202A');
    doc.setLineWidth(0.6);
    doc.setLineDash([1, 1], 0);
    doc.line(30, doc.lastAutoTable.finalY + 3, 180, doc.lastAutoTable.finalY + 3);
    //doc.setLineWidth(1);
    doc.setLineDash([0, 0], 0);
}

export function tableBoard(doc, data) {
    let tempData = [
        {
            one: "REFERENCIA",
            two: data.reference,
        },
        {
            one: "NOMENCLATURA",
            two: data.nomenclature.toString().toUpperCase(),
        },
        {
            one: "LOCALIZACIN",
            two: data.location.toString().toUpperCase(),
        },
    ];
    if (data.typeBoard) {
        tempData.push(
            {
                one: "TIPO DE TABLERO",
                two: data.typeBoard.toString().toUpperCase(),
            }
        )
    }
    if (data.ability) {
        tempData.push(
            {
                one: "CAPACIDAD",
                two: data.ability.toString().toUpperCase(),
            }
        )
    }
    if (data.brand) {
        tempData.push(
            {
                one: "MARCA",
                two: data.brand.toString().toUpperCase(),
            }
        )
    }

    autoTable(doc, {
        theme: 'plain',
        body: tempData,
        columnStyles: {
            one: { cellWidth: 30 },
        },

        columns: [
            { dataKey: "one" },
            { dataKey: "two" },

        ],
        styles: { fontSize: sizeFontTable, textColor: "#17202A", cellPadding: 0.5 },
        margin: { left: 30, right: 35, },
    })
}

export function text(doc, size, text, x, y, percent) {
    doc.setFontSize(size);
    if (percent) {
        doc.text(text, x * 6.773, y * 3.81);
    } else {
        doc.text(text, x, y);
    }
}

export function textTable(doc, text) {
    autoTable(doc, {
        theme: 'plain',
        body: [[text]],
        styles: { fontSize: 12, textColor: "#17202A", fontStyle: 'bold', cellWidth: 187 },
    })
}

export function textTableSize(doc, text, size, cellWidth, x, y) {
    let fontSize = sizeFontTable;
    let cellWidthIn = 187;
    if (size) fontSize = size;
    if (cellWidth) cellWidthIn = cellWidth;

    autoTable(doc, {
        startY: y,
        theme: 'plain',
        body: [[text]],
        styles: { fontSize: fontSize, textColor: "#17202A", cellWidth: cellWidthIn },
        margin: { left: x }
    })
}

export function superTableBoardItems(doc, data) {

    if (!data) return 0;
    if (!data.boardList) return 0;
    if (!data.boardList.data) return 0;
    if (data.boardList.data.length == 0) return 0;
    doc.addPage();
    textTable(doc, 'NO CONFORMIDADES - TABLEROS ELCTRICOS');
    const list = data.boardList.data;
    listBoard(doc, list);
}
//primero sacar los items que no cumplen...
function listBoard(doc, list) {
    list.forEach(element => {
        let note = element.result.find(f => f.content == "ItemNote");
        const cleanData = filterItems(element.result);
        if (cleanData.length > 0) {
            tableBoard(doc, element)
            observation(doc, cleanData, note)
            allNoms = [...allNoms, ...cleanData];
        }
    });
}

function filterItems(items) {
    const filter = items.filter(i => i.value == 'no');
    if (filter.length == 0) return 0;
    //Crear nuevo array con datos filtrados por medio del map..
    const cleanData = filter.map(i => {
        return {
            one: filterSubText(i),
            two: i.nom,
        }
    })
    return cleanData;
}
export function noConformitiesGeneral(doc, data) {

    let tempdata = '';
    if (data.generalNonconformities) {
        tempdata = data.generalNonconformities;
    }
    if (tempdata.replace(/ /g, "") == "") return 0;
    doc.addPage();
    textTable(doc, 'NO CONFORMIDADES - GENERAL');
    autoTable(doc, {
        theme: 'plain',
        body: [{ tempdata }],
        styles: { fontSize: sizeFontTable, textColor: "#17202A", cellPadding: 2 },
        margin: { left: 30, right: 30, },
    })

}

export function regulatorySupport(doc, glossarySub) {
    const validateBoard = !allNoms || allNoms.length == 0;
    const validateSub = !glossarySub || glossarySub.length == 0;
  /*   console.log(`Validacion Tablero ${validateBoard}`)
    console.log(`Validacion Subestacion ${validateSub}`) */
    if(validateBoard && validateSub) return 0;
    //if ((!allNoms || allNoms.length == 0) || (!glossarySub || glossarySub.length == 0)) return 0;
    doc.addPage();

    textTable(doc, 'SOPORTE NORMATIVO NORMA OFICIAL MEXICANA NOM-001-SEDE-2012, INSTALACIONES ELCTRICAS (UTILIZACIN).');

    const onlyNom = [...new Set(allNoms.map(i => i.two))];
    let count = 0;

    let cleanDataRegulatory = onlyNom.map(i => {
        if (textRegulatory.find(e => e.two == i) !== undefined) {
            count++
            return {
                one: `${count}.`,
                two: (textRegulatory.find(e => e.two == i)).one,
            }
        }
        return 0;
    })
    if (glossarySub && glossarySub.length > 0) {
        glossarySub.forEach(e => {
            count++;
            cleanDataRegulatory.push(
                {
                    one: `${count}.`,
                    two: e.one,
                }

            )
        }
        )
    }
    regulatorySupportTable(doc, cleanDataRegulatory)
}

