//Funcion escoger sub texto...
function filterSubText(i) {
    //if (i.content != 'ItemBasic' && i.content != 'ItemThermography')
    if (i.content == 'ItemBasicSelectNote') {
        const getText = textReport.find(e => e.two == i.nom).one;
        if (i.note.replace(/ /g, "") == "") return `${getText.find(q => q.a == i.model).b}.`;
        return `${getText.find(q => q.a == i.model).b}:\n${i.note}`
    }
    if (i.content == 'ItemBasicSelect') {
        const getText = textReport.find(e => e.two == i.nom).one;
        return getText.find(q => q.a == i.model).b
    }
    return textReport.find(e => e.two == i.nom).one
}


